.button-container {
	text-align: center;
	margin-top: -20px;
	margin-bottom: 20px;
}

.glass {
	padding: 8px 15px;
	margin: 5px;
	font-size: 20px;
	text-align: center;
	cursor: pointer;
	outline: none;
	color: #fff;
	background-color: #4875B4;
	background-image: -webkit-gradient(linear, left top, left bottom, from(blue), to(#4875B4));
	background-image: -webkit-linear-gradient(blue, #4875B4);
	background-image: -o-linear-gradient(blue, #4875B4);
	background-image: linear-gradient(blue, #4875B4);
	border: none;
	border-radius: 15px;
	-webkit-box-shadow: 0 9px rgba(0,0,0,0.8);
	        box-shadow: 0 9px rgba(0,0,0,0.8);
}

.glass:hover {
	background-image: -webkit-gradient(linear, left top, left bottom, from(darkblue), to(#4875B4));
	background-image: -webkit-linear-gradient(darkblue, #4875B4);
	background-image: -o-linear-gradient(darkblue, #4875B4);
	background-image: linear-gradient(darkblue, #4875B4);
}

.glass:active {
	background-image: -webkit-gradient(linear, left top, left bottom, from(darkblue), to(#4875B4));
	background-image: -webkit-linear-gradient(darkblue, #4875B4);
	background-image: -o-linear-gradient(darkblue, #4875B4);
	background-image: linear-gradient(darkblue, #4875B4);
	-webkit-box-shadow:  0 4px rgba(0,0,0,0.8);
	        box-shadow:  0 4px rgba(0,0,0,0.8);
	-webkit-transform: translateY(4px);
	    -ms-transform: translateY(4px);
	        transform: translateY(4px);
}


