.weather-container-out {
	width: 100%;
}

.weather-container {
	border: 5px solid black;
	border-radius: 10px;
	-webkit-box-shadow: 0px 0px 50px 1px black;
	        box-shadow: 0px 0px 50px 1px black;
	position: relative;
	left: 50%;
	max-width: 500px;
	-webkit-transform: translateX(-50%);
	    -ms-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.information {
}

.weather {
	width: 100%;
	border: rgba(0,0,0,0.8);
}

.sun { 
	z-index: -2;
	position: relative;
	top: 40%;
	left: 75%;
	-webkit-transform: translate(-30%, -140%);
	    -ms-transform: translate(-30%, -140%);
	        transform: translate(-30%, -140%); 
	width:50px;
	height:50px;
	border-radius:50%;	
	background:white;
	opacity:0.9;			
	-webkit-box-shadow: 0px 0px 40px 15px white;			
	        box-shadow: 0px 0px 40px 15px white;  
}

.moon {
	z-index: -2;
	height: 50px;
	width: 50px;
	border-radius: 50%;
	background-color: #fff;
	border: 8px solid transparent;
	-webkit-box-shadow: 0px 0px 50px rgba(157, 229, 253, 0.59);
	        box-shadow: 0px 0px 50px rgba(157, 229, 253, 0.59);
	position: relative;
	top: 40%;
	left: 75%;
	-webkit-transform: translate(-30%, -140%);
	    -ms-transform: translate(-30%, -140%);
	        transform: translate(-30%, -140%); 
}

.weather-img {
	width: 100%;
	margin-bottom: -5px;
	margin-top: -100px;
}

.emphasize {
	font-weight: bold;
	font-size: 1.2em;
}


