body {
	width: 100%;
	height: 100%;
}

.app {
	height: 100%;
	width: 100%;
	min-width: 300px;
	color: rgba(255,255,255, 1);
}

.app-content {
	padding: 10px;
}

.weather-content {
	width: 100%;
	padding: 0;
	margin: 0;
}

h1, h2, h3, p {
	text-align: center;
}

h2 {
	margin-top: -0.5px;
}

.default-background {
	background: -webkit-gradient(linear, left top, right top, from(#fca4a5), to(#067327));
	background: -webkit-linear-gradient(left, #fca4a5, #067327);
	background: -o-linear-gradient(left, #fca4a5, #067327);
	background: linear-gradient(to right, #fca4a5, #067327);
}

.moon-background {
	height: 100%;
	padding-bottom: 100px;
	background-color: rgb(3, 20, 39);
	background-image:
		-webkit-radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 0px),
		-webkit-radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 0px),
		-webkit-radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 0px),
		-webkit-radial-gradient(rgba(255,255,255,.2), rgba(255,255,255,.1) 2px, transparent 0px);
	background-image:
		-o-radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 0px),
		-o-radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 0px),
		-o-radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 0px),
		-o-radial-gradient(rgba(255,255,255,.2), rgba(255,255,255,.1) 2px, transparent 0px);
	background-image:
		radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 0px),
		radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 0px),
		radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 0px),
		radial-gradient(rgba(255,255,255,.2), rgba(255,255,255,.1) 2px, transparent 0px);
	background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px; 
}

.sun-background {
	padding-bottom: 100px;
	height: 100%;
	background: -webkit-gradient(linear, left top, left bottom, from(#ebc030), to(#0080ff));
	background: -webkit-linear-gradient(#ebc030, #0080ff);
	background: -o-linear-gradient(#ebc030, #0080ff);
	background: linear-gradient(#ebc030, #0080ff);
}

@media(max-height: 480px) {
	.moon-background, .sun-background {
		padding-bottom: 250px;
	}
}

@media(max-height: 300px) {
	.moon-background, .sun-background {
		padding-bottom: 350px;
	}
}


